import React, {useEffect, useState} from "react";
import Modal from 'react-modal';
import moment from "moment";
import DateTimePicker from 'react-datetime-picker';
import Swal from "sweetalert2";
import {useDispatch, useSelector} from "react-redux";
import {uiCloseModal} from "../../actions/ui";
import {eventStartAddNew, eventClearActiveEvent, eventUpdated, eventStartUpdated} from "../../actions/events";

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',

    },
    overlay: {
        zIndex: 11,
    },
};

Modal.setAppElement('#root');
const now = moment().minutes(0).seconds(0).add(1,'hours');
const nowPlus1 = now.clone().add(1,'hours');

const initEvent = {
    title: '',
    notes: '',
    start: now.toDate(),
    end: nowPlus1.toDate(),
};

export const CalendarModal = ({event}) => {

    //const [isOpen, setIsOpen] = useState(false);
    const [dateStart, setDateStart] = useState(now.toDate());
    const [dateEnd, setDateEnd] = useState(nowPlus1.toDate());
    const [titleValid, setTitleValid] = useState(true);

    const dispatch = useDispatch();
    const {modalOpen} = useSelector( state => state.ui );
    const {activeEvent} = useSelector( state => state.calendar );

    const [formValues, setFormValues] = useState({
        title: 'Evento',
        notes: '',
        start: now.toDate(),
        end: nowPlus1.toDate(),
    });

    const {notes, title, start, end} = formValues;

    useEffect(() => {
        if(activeEvent){
            setFormValues(activeEvent);
        } else {
            setFormValues(initEvent);
        }
    }, [activeEvent, setFormValues]);

    const handleInputChange = ({target}) => {
        setFormValues({
            ...formValues,
            [target.name]: target.value,
        });

        console.log(formValues);
    };

    const closeModal = () => {
        console.log('cerrar modal')
        dispatch(uiCloseModal())
        setFormValues(initEvent);
        dispatch(eventClearActiveEvent());
    };

    const onChange = (e) => {
        console.log("change...");
        setDateStart(e);
        setFormValues({
            ...formValues,
            start: e,
        });
    };

    const onChangeEnd = (e) => {
        console.log("change...");
        setDateEnd(e);
        setFormValues({
            ...formValues,
            end: e,
        });
    };



    const handleSubmitForm = (e) => {
        e.preventDefault();
        const momentStart = moment(start);
        const momentEnd = moment(end);

        if(momentStart.isSameOrAfter(momentEnd)){
            console.log('Fecha 2 debe de ser mayor');
            return Swal.fire('Error', 'La fehca fin debe de ser mayor a la de inicio', 'error');
        }
        if(title.trim() === ''){
            setTitleValid(false);
            return  false;
        }

        /*formValues.user = {
            _id: '123ss',
            name: 'Jesus'
        }*/

        if(activeEvent){
            dispatch(eventStartUpdated(formValues));
            //dispatch(eventUpdated(formValues));
        } else {
            
            dispatch(eventStartAddNew({
                ...formValues,
            }));
            /*dispatch(eventAddNew({
                ...formValues,
                id: new Date().getTime(),
            }));*/
        }


        setTitleValid(true);
        closeModal();
    };

    return(
        <div>

            <Modal
                isOpen={modalOpen}
                /*onAfterOpen={afterOpenModal}*/
                onRequestClose={closeModal}
                style={customStyles}
                closeTimeoutMS={200}
                className={"modal"}
                overlayClassName={"modal-fondo"}
            >
                <h1> {(activeEvent)? 'Editar' : 'Nuevo'} evento </h1>
                <hr />
                <form className="container"
                    onSubmit={handleSubmitForm}
                >

                    <div className="form-group">
                        <label>Fecha y hora inicio</label>
                        <DateTimePicker
                            onChange={onChange}
                            value={dateStart}
                            className={'form-control'}
                        />
                    </div>

                    <div className="form-group">
                        <label>Fecha y hora fin</label>
                        <DateTimePicker
                            onChange={onChangeEnd}
                            value={dateEnd}
                            minDate={dateStart}
                            className={'form-control'}
                        />
                    </div>

                    <hr />
                    <div className="form-group">
                        <label>Titulo y notas</label>
                        <input
                            type="text"
                            className={`form-control ${!titleValid && 'is-invalid'}`}
                            placeholder="Título del evento"
                            name="title"
                            autoComplete="off"
                            value={title}
                            onChange={handleInputChange}
                        />
                        <small id="emailHelp" className="form-text text-muted">Una descripción corta</small>
                    </div>

                    <div className="form-group">
                        <textarea
                            type="text"
                            className="form-control"
                            placeholder="Notas"
                            rows="5"
                            name="notes"
                            value={notes}
                            onChange={handleInputChange}
                        ></textarea>
                        <small id="emailHelp" className="form-text text-muted">Información adicional</small>
                    </div>

                    <button
                        type="submit"
                        className="btn btn-outline-primary btn-block"
                    >
                        <i className="far fa-save"></i>
                        <span> Guardar</span>
                    </button>

                </form>
            </Modal>
        </div>
    )
}